import React, { Component } from 'react';
import { Link, Route, Switch} from 'react-router-dom'


import MenuBar from './components/MenuBar'
import Resume from './components/Resume'

import {withRouter} from 'react-router-dom';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      percent: 0, 
      blur: "",
      selected: "0",
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
        if(this.props.location.pathname == "/resume") {
          this.setState({
            blur: "blur(3px)",
            selected: 1
          })
        } else {
          this.setState({
            blur: "",
            selected: 0
          
        })
        }
    }
  }

 

  handleScroll(percent) {
    this.setState({percent})
  }


  render() {
    return (
      <div>
        <MenuBar percent={this.state.percent} selected={this.state.selected}/>

          <Route path="/" render={props => 
            <div>
              {/* <Welcome onScrollChange={this.handleScroll.bind(this)} blur={this.state.blur}/> */}

              <Route path="/resume" render={props => 
                <div>
                  <Resume />
                  
                </div>
              } />
            </div>
          } />


        
      </div>
    );
  }
}

export default withRouter(props => <App {...props}/>);



